import styled from "styled-components";
import React from "react";
const StyledSecondaryButton = styled.button`
  display: inline-block;
  background: #dbdbdb;
  color: #555;
  padding: 1rem 1.6rem;
  border-radius: 20px;
  border: none;
  margin: 1rem;
  transition: all 0.3s;
  cursor: pointer;
  font-size: 1.3rem;
  :hover {
    background: #cecece;
  }
`;

export const SecondaryButton = props => (
  <StyledSecondaryButton onClick={props.clicked}>
    {props.children}
  </StyledSecondaryButton>
);
