import React, { useEffect, useState } from "react";
import { InputComp, Label } from "../../../UI/Form/styled";
import styled from "styled-components";
import { MainButton } from "../../../UI/MainButton";
import { SecondaryButton } from "../../../UI/SecondaryButton";
const ChangeItemContainer = styled.div`
  margin: 3rem auto;
`;
const ButtonBar = styled.div`
  margin: 3rem auto 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
`;

export const ChangeItem = props => {
  const [name, setName] = useState("");
  const [barCode, setBarCode] = useState(0);
  const [supply, setSupply] = useState(0);
  useEffect(() => {
    setName(props.selected.name);
    setBarCode(props.selected.code);
    setSupply(props.selected.supply);
  }, []);

  return (
    <ChangeItemContainer>
      <Label>Product naam</Label>
      <InputComp
        type="text"
        value={name}
        onChange={e => setName(e.target.value)}
      ></InputComp>
      <Label>Product naam</Label>
      <InputComp
        type="number"
        value={barCode}
        onChange={e => setBarCode(parseInt(e.target.value))}
      ></InputComp>
      <Label>Product naam</Label>
      <InputComp
        type="number"
        value={supply}
        onChange={e => setSupply(parseInt(e.target.value))}
      ></InputComp>
      <ButtonBar>
        <SecondaryButton clicked={props.cancel}>Cancel</SecondaryButton>
        <MainButton
          clicked={() =>
            props.save({ ...props.selected, name, code: barCode, supply })
          }
        >
          Save
        </MainButton>
      </ButtonBar>
    </ChangeItemContainer>
  );
};
