import React, { useState, useEffect } from "react";
import axios from "axios";
import { createGlobalStyle } from "styled-components";
import { BrowserRouter } from "react-router-dom";
import { Layout } from "./components/layout";
import { List } from "./components/views/home/List";
import { Adder } from "./components/views/home/AddNew";
import { Notifications } from "./components/UI/Notifications";
import { Popup } from "./components/UI/Popup";
import { ChangeItem } from "./components/views/home/ChangeItem";
const rtbf = ""; // || "http://localhost:5000"
const Base = createGlobalStyle`
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  outline: none;
}
html{
  font-size: 67.5%;
  font-family: "Ubuntu", sans-serif;
}
body{
  color: #555;
  background: linear-gradient(#54c654, #91d159);
  background-repeat: no-repeat;
  min-height: 100vh;
}
::-moz-selection { /* Code for Firefox */
color: #fff;
background: #54c654;
}

::selection {
color: #fff;
background: #54c654;
}
`;

function App() {
  const [articles, setArticles] = useState([]);
  const [productName, setName] = useState("");
  const [barCode, setBarCode] = useState("");
  const [notifications, setNotifications] = useState([]);
  const [selected, setSelected] = useState({});
  //  useEffect(() => console.log(notifications), [notifications]);
  //const [prodNumber, setNumber] = useState(null);
  const addNotifictation = ({ message, type }) => {
    const notification = { message, type, id: Date.now() + Math.random() };
    let newNotifications = [...notifications].concat(notification);
    setNotifications(newNotifications);
    setTimeout(() => {
      newNotifications = [...notifications].filter(
        n => n.id !== notification.id
      );
      setNotifications(pre => pre.filter(n => n.id !== notification.id));
    }, 5000);
  };
  const removeNotification = i => {
    const newNotifications = [...notifications];
    newNotifications.splice(i, 1);
    setNotifications(newNotifications);
  };

  const fetcher = async () => {
    const res = await axios.get(rtbf + "/articles");
    setArticles(res.data.articles);
  };
  const addProduct = async (code = barCode) => {
    // console.log(code);
    // console.log("barcode", barCode);
    // console.log(productName);

    articles.filter(art => {
      console.log(typeof art.code, typeof code);
      return art.code === code;
    });

    if (
      articles.filter(art => parseInt(art.code) === parseInt(code)).length ===
        0 &&
      productName === ""
    ) {
      return addNotifictation({
        message: "Je moet een naam invullen als je een nieuw product toevoegd",
        type: "error"
      });
    }
    if (code.length < 13) {
      return addNotifictation({
        message: "Barcode moet 13 cijfers bevatten",
        type: "error"
      });
    }
    await axios.post(rtbf+"/article", {
      name: productName,
      code: code
    });
    addNotifictation({
      message: "Product successvol toegevoegd",
      type: "success"
    });
    fetcher();
  };
  const handleBarCodeChange = (e, mode) => {
    if (mode === "scan" && e.toString().length === 13) {
      const code = e;

      addProduct(parseInt(code));
      setBarCode("");
      return;
    }
    setBarCode(parseInt(e));
    console.log(e);
  };
  const select = item => {
    setSelected(item);
  };
  const save = async item => {
    await axios.patch(`${rtbf}/article/${item._id}`, item);
    addNotifictation({ message: "Updaten item successvol", type: "success" });
    fetcher();
    setSelected({});
  };
  const unSelect = e => {
    if (
      e.target.localName === "span" ||
      e.target.localName === "button" ||
      e.target.className.includes("backdrop")
    ) {
      setSelected({});
    }
  };
  useEffect(() => {
    fetcher();
    console.log("new version!");
  }, []);
  useEffect(() => {
    console.log(barCode);
  }, [barCode]);
  return (
    <BrowserRouter>
      <Base></Base>
      <Layout>
        <List articles={articles} select={select}></List>
        <Adder
          setName={setName}
          setBarCode={handleBarCodeChange}
          addProduct={addProduct}
          barCode={barCode}
          productName={productName}
          // setNumber={setNumber}
        ></Adder>
      </Layout>
      <Notifications
        removeNotification={removeNotification}
        notifications={notifications}
      ></Notifications>
      {Object.keys(selected).length > 0 ? (
        <Popup unselect={unSelect}>
          <ChangeItem
            selected={selected}
            cancel={unSelect}
            save={save}
          ></ChangeItem>
        </Popup>
      ) : null}
    </BrowserRouter>
  );
}

export default App;
