import styled from "styled-components";
import React from "react";
const Backdrop = styled.div`
  width: 100%;
  position: fixed;
  height: 100%;
  background: rgba(0, 0, 0, 0.2);
  top: 0;
  left: 0;
  z-index: 90;
`;

const StyledPopup = styled.div`
  z-index: 91;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  width: 50%;
  min-height: 10rem;
  padding: 2rem;
  border-radius: 5px;
  span {
    position: absolute;
    top: 1rem;
    right: 2rem;
    font-size: 3rem;
    cursor: pointer;
  }
`;

export const Popup = props => {
  return (
    <Backdrop onClick={props.unselect} className="backdrop">
      <StyledPopup>
        {props.children} <span>&times;</span>
      </StyledPopup>
    </Backdrop>
  );
};
