import styled from "styled-components";
import React from "react";
const StyledMainButton = styled.button`
  display: inline-block;
  background: #54c654;
  color: #fff;
  padding: 1rem 2rem;
  border-radius: 20px;
  border: none;
  margin: 2rem 1rem;
  transition: all 0.3s;
  cursor: pointer;
  font-size: 1.3rem;
  :hover {
    background: #45b545;
  }
`;

export const MainButton = props => (
  <StyledMainButton onClick={props.clicked}>{props.children}</StyledMainButton>
);
