import React from "react";
import { NotificationContainer, Notification } from "./styled";

export const Notifications = ({ notifications, removeNotification }) => {
  return (
    <NotificationContainer>
      {notifications.map((n, i) => {
        return (
          <Notification
            key={i}
            type={n.type}
            onClick={() => removeNotification(i)}
          >
            {n.message}
          </Notification>
        );
      })}
    </NotificationContainer>
  );
};
