import React from "react";
import { PageContainer } from "../../../UI/ViewContainer";
import { ListContainer, Row } from "./styled";
export const List = ({ articles, select }) => {
  return (
    <PageContainer>
      <ListContainer>
        <Row>
          <div>Productnaam</div>
          <div>Barcode</div>
          <div>Voorraad</div>
        </Row>
        {articles &&
          articles.map((prod, index) => (
            <Row key={index} onClick={() => select(prod)}>
              <div>{prod.name}</div>
              <div>{prod.code}</div>
              <div>{prod.supply}</div>
            </Row>
          ))}
      </ListContainer>
    </PageContainer>
  );
};
