import React, { useState } from "react";
import { ChangeModeBar, ChangeModeButton } from "./styled";
import { PageContainer } from "../../../UI/ViewContainer";
import { MainButton } from "../../../UI/MainButton";
import { InputComp, Label } from "../../../UI/Form/styled";
export const Adder = props => {
  const { setName, setBarCode, addProduct, barCode, productName } = props;
  const [mode, toggleMode] = useState("scan");
  return (
    <PageContainer>
      <ChangeModeBar>
        <ChangeModeButton
          onClick={() => toggleMode("manual")}
          active={mode === "manual"}
        >
          Manueel
        </ChangeModeButton>
        <ChangeModeButton
          onClick={() => toggleMode("scan")}
          active={mode === "scan"}
        >
          Scan
        </ChangeModeButton>
      </ChangeModeBar>
      {mode === "manual" ? (
        <>
          <Label>
            Productnaam (Alleen invullen als je een nieuw product toevoegd)
          </Label>
          <InputComp
            type="text"
            placeholder="Productnaam"
            onChange={e => setName(e.target.value)}
            value={productName}
          ></InputComp>
          <Label>Barcode*</Label>
          <InputComp
            type="number"
            placeholder="Barcode"
            onChange={e => setBarCode(e.target.value, mode)}
            value={barCode}
          ></InputComp>
          <MainButton clicked={() => addProduct(barCode)}>
            Product toevoegen
          </MainButton>
        </>
      ) : (
        <>
          <Label>Barcode*</Label>
          <InputComp
            type="number"
            placeholder="Barcode"
            onChange={e => setBarCode(e.target.value, mode)}
            value={barCode}
          ></InputComp>
        </>
      )}
    </PageContainer>
  );
};
